import React from 'react';
import Block from './Block';
import Badge from './Badge';
import { formatPrice } from '../utility/format';
import { getChangesString, getDurationString } from '../utility/get';

import PersonIcon from '../data/icon/person';
import LargeChevron from '../data/icon/large-chevron';
import Arrow from '../data/icon/arrow';
import logo from '../data/logo.svg';

import './ServiceResult.css';

export default class ServiceResult extends React.Component {
  constructor(props) {
    super(props);
    
    this.handleClick = this.handleClick.bind(this);
  }
  
  getBadges() {
    const { data } = this.props;

    const elements = [];

    if (data.isCheapest) {
      elements.push(<Badge key="cheapest" cheapest text="Cheapest" />);
    }
    if (data.isFastest) {
      elements.push(<Badge key="fastest" fastest text="Fastest" />);
    }
    
    if (elements.length > 0) {
      return (
        <div className="ServiceResult-horizontal ServiceResult-badges">
          {elements}
        </div>
      );
    }

    return null;
  }

  handleClick() {
    const { data, onClick } = this.props;

    if (data.tickets.length > 0) {
      onClick();
    }
  }

  render() {
    const { data, onServiceInfoClick } = this.props;

    return (
      <Block className="ServiceResult" onClick={this.handleClick}>
        <div className="ServiceResult-horizontal ServiceResult-top">
          <div className="ServiceResult-top-content">
            <div className="ServiceResult-topLine">
              {this.getBadges()}
              {data.price.standard && <div className="ServiceResult-priceLabel">
                <PersonIcon className="ServiceResult-personIcon" />
                <b>{data.passengers}&nbsp;</b>
                Return from
              </div>}
            </div>
            <div className="ServiceResult-horizontal">
              <div className="ServiceResult-details">
                <div className="ServiceResult-stations">
                  <div className="ServiceResult-station">
                    <div className="ServiceResult-station-time">
                      {data.time.departure.format('HH:mm')}
                    </div>
                    <div className="ServiceResult-station-name">
                      {data.legs[0].origin}
                    </div>
                  </div>
                  <Arrow className="ServiceResult-station-arrow" />
                  <div className="ServiceResult-station">
                    <div className="ServiceResult-station-time">
                      {data.time.arrival.format('HH:mm')}
                    </div>
                    <div className="ServiceResult-station-name">
                      {data.legs[data.legs.length - 1].destination}
                    </div>
                  </div>
                </div>
                {data.note && <div className="ServiceResult-note">
                  {data.note}
                </div>}
              </div>
              <div className="ServiceResult-prices">
                {data.price.standard && <div className="ServiceResult-prices-standard">
                  {formatPrice(data.price.standard)}
                </div>}
                {data.price.first && <div className="ServiceResult-prices-first">
                  1st {formatPrice(data.price.first)}
                </div>}
              </div>
            </div>
          </div>
          <LargeChevron className="ServiceResult-chevron" />
        </div>
        <div className="ServiceResult-bottom">
          <div className="ServiceResult-bottom-left">
            {(data.toc === 'GR') && <img src={logo} className="ServiceResult-logo" />}
            <div className="ServiceResult-length">
              {getDurationString(data.time.departure, data.time.arrival)}, {getChangesString(data.changes)}
            </div>
          </div>
          <div className="ServiceResult-info" onClick={e => {onServiceInfoClick(); e.stopPropagation()}}>
            Journey info
          </div>
        </div>
      </Block>
    )
  }
}
