import React from 'react';
import BackIcon from '../data/icon/back';
import AdultIcon from '../data/icon/adult';
import ChildIcon from '../data/icon/child';
import RailcardIcon from '../data/icon/railcard';

import './Header.css';

export default class Header extends React.Component {
  getBackButton() {
    const { noBack, onBackClick } = this.props;
    
    if (noBack) {
      return <div className="Header-left Header-button" />
    }
    else {
      return (
        <button className="Header-left Header-button" onClick={onBackClick}>
          <BackIcon className="Header-button-icon" />
          <div className="Header-button-text">
            Back
          </div>
        </button>
      );
    }
  }

  getCloseButton() {
    const { hasClose, onCloseClick } = this.props;
    
    if (hasClose) {
      return (
        <button className="Header-right Header-button" onClick={onCloseClick}>
          Close
        </button>
      );
    }
    else {
      return <div className="Header-right Header-button" />
    }
  }

  getTitle() {
    const { data } = this.props;
    
    return (
      <div className="Header-middle">
        {data.title && <div className="Header-title">
          {data.title}
        </div>}
        {data.subtitle && <div className="Header-subtitle">
          {data.subtitle}
        </div>}
      </div>
    );
  }

  getSubheader() {
    const { data } = this.props;

    if (!data.hasSubheader) return null;

    return (
      <div className="Header-sub">
        {data.adults > 0 && <div className="Header-sub-item">
          <AdultIcon className="Header-sub-item-icon" />
          <div className="Header-sub-item-text">
            {data.adults} {data.adults === 1 ? 'Adult' : 'Adults'}
          </div>
        </div>}
        {data.children > 0 && <div className="Header-sub-item">
          <ChildIcon className="Header-sub-item-icon" />
          <div className="Header-sub-item-text">
            {data.children} {data.adults === 1 ? 'Child' : 'Children'}
          </div>
        </div>}
        {data.railcards > 0 && <div className="Header-sub-item">
          <RailcardIcon className="Header-sub-item-icon" />
          <div className="Header-sub-item-text">
            {data.railcards} {data.railcards === 1 ? 'Railcard' : 'Railcards'}
          </div>
        </div>}
      </div>
    );
  }

  getPadder() {
    const { data } = this.props;
    
    return (
      <React.Fragment>
        <div className="Header-padder-main" />
        {data.hasSubheader && <div className="Header-padder-sub" />}
      </React.Fragment>
    )
  }

  render() {
    return (
      <React.Fragment>
        <header className="Header">
          <div className="Header-main">
            {this.getBackButton()}
            {this.getTitle()}
            {this.getCloseButton()}
          </div>
          {this.getSubheader()}
        </header>
        {this.getPadder()}
      </React.Fragment>
    )
  }
}
